import React from 'react'
import Main_Componet from '../Main_Componet'
import firebase from 'firebase/app'
import 'firebase/database'
import M from 'materialize-css'
import { Redirect } from 'react-router'

class Login extends Main_Componet {
	constructor(props){
    super(props)
    firebase.initializeApp(this._config().firebase_config);
    this.db = firebase.database();
    this.state = {
      config      : this._config(),
      user_id     : localStorage.getItem("user_id"),
      redirect    : false,
      isLoaded      : false,
      submit      : false
    }
    this.login.bind(this);
  }
  componentDidMount(){
  	const {user_id}  = this.state;
  	var update_state  = { redirect : false , isLoaded : true};
  	if(user_id){
  		update_state.redirect = true;
  	}
  	this.setState(update_state);
  	/*this.db.ref('users/'+user_id).once('value', (snapshot)=>{
  		var update_state  = { redirect : false , isLoaded : true};
			if(snapshot.val() !== null){
				update_state.redirect = true;
			}
			this.setState(update_state);
		});*/
  }
  login = (event) => {
  	const form_data = {
  		username : event.target[0].value,
  		password : event.target[1].value
  	};
  	this.db.ref('users').on('value', (snapshot)=>{
  		var result = snapshot.val();
  		var toastHTML = "<span>Invalid Username or Password</span>";
  		for (var id in result) {
        var row = result[id];
        if(form_data.username === row.username && form_data.password === row.password){
        	if(row.active){
        		toastHTML = "<span>Hi, Welcome "+row.name+"</span>";
        		localStorage.setItem("user_id", row.id);
        		localStorage.setItem("user_name", row.name);
        		// this.props.history.push("/todo");
        		this.setState({submit : true});
        	}
        	else{
        		toastHTML = "<span>Your account has been inactivated. Please contact your administrator.</span>";
        	}
        }
      }
      M.toast({html: toastHTML});
  	});
  }
  render() {
  	const { redirect, isLoaded, submit } = this.state;
  	if(!isLoaded){
  		return <div className="loading">Loading...</div>;
  	}
  	else if((isLoaded && redirect ) || submit){
			return <Redirect to="/todo" />;
  	}
    return (
      <div className="container">
	      <h2>Login</h2>
        <form action="" id="login" onSubmit={e => {this.login(e); e.preventDefault();}} method="post" >
        	<div className="input-field">
				    <input placeholder="Username" defaultValue="admin"
				    id="username" type="text" required className="validate" />
				    <label forhtml="username">Username</label>
				    <span className="helper-text" data-error="Required field"></span>				          
				  </div>
				  <div className="input-field">
				    <input placeholder="Password" 
				    defaultValue="admin"
				    id="password" type="password" required className="validate" />
				    <label forhtml="password">Password</label>
				    <span className="helper-text" data-error="Required field"></span>
				  </div>	
				  <div className="input-field">
				  	<button type="submit" className="waves-effect waves-light btn">Login</button>
				  </div>
        </form>   
	      
      </div>
    )
  }
}
export default Login


