import React from 'react'
import Main_Componet from '../Main_Componet'
import firebase from 'firebase/app'
import 'firebase/database'
import M from 'materialize-css'
import { Redirect } from 'react-router'
var form_values = {
  id          : "",
  title       : "",
  note        : "",
  check_item  : [],
  completed   : false,
};
class ToDo extends Main_Componet {
  constructor(props){
    super(props)
    firebase.initializeApp(this._config().firebase_config);
    this.db = firebase.database();
    this.state = {
      config      : this._config(),
      todo        : [],
      edit        : "",
      clear_all   : false,
      form_values : form_values,
      page_access      : true
    }
    this.check_list_click.bind(this);
    this.submit_form.bind(this);
    this.check_list_change.bind(this);
    this.remove_check_list.bind(this);
    this.remove_todo.bind(this);
    this.edit_todo.bind(this);
    this.reset_form.bind(this);
    this.field_change.bind(this);
  }
  componentDidMount(){
    // localStorage.removeItem("user_id");
    var fixed_action = document.querySelectorAll('.fixed-action-btn');
    M.FloatingActionButton.init(fixed_action, {});
    var modal        = document.querySelectorAll('.modal');
    M.Modal.init(modal, {});   
    this.get_todo();
  }
  submit_form = (event) => {
    var { form_values, edit} = this.state;
    var checked         = 0;
    if(form_values.check_item){   
      form_values.check_item.forEach(function(element, index){
        if(element.status){
          checked++;
        }
      });
    }
    form_values.completed = (form_values.check_item && form_values.check_item.length !== 0 && form_values.check_item.length === checked)?true:false;
    var id                = new Date().getTime();
    if(edit !== ''){
      id = edit;
    }
    form_values.id = id;
    this.db.ref('todo/'+id).set(form_values);
    let instance = M.Modal.getInstance(this.Modal);
    instance.close();
  }
  remove_todo = (row, clear_all = false) => {
    var id = clear_all ? "" : row.id;
    this.setState({
      undo      : row,
      clear_all : clear_all
    });
    this.db.ref('todo/'+id).remove();
    var toastHTML = '<span>Todo successfully removed. </span><button class="btn-flat undo toast-action">Undo</button>';
    M.toast({html: toastHTML });
  }
  check_list_click =  async (id, sub_index) => {
    const snapshot     = await this.db.ref('todo/'+id).once('value');
    const current_item = snapshot.val();
    current_item['check_item'][sub_index]['status'] = !current_item['check_item'][sub_index]['status'];
    var inc     = 0;
    var checked = 0;
    if(current_item.check_item && current_item.check_item.length){
      current_item.check_item.forEach(function(element, index){
        if(element.status){
          checked++;
        }
        inc++
      });
    }
    current_item['completed'] = false;
    if(checked === inc){
      current_item['completed'] = true;
    }
    this.db.ref('todo/'+id).set(current_item);
  }
  get_todo = () => {
    const self = this;
    this.db.ref('todo').on('value', (snapshot)=>{
      var todo = [];
      var value = snapshot.val();
      for (var key in value) {
        todo.push(value[key]);
      }
      self.setState({todo : todo});
    });
  }
  field_change = (field, value) =>{
    var { form_values } = this.state;
    form_values[field]  = value;
    this.setState({ form_values : form_values });
  }
  check_list_change = (e, action = false) => {
    if((e.target.value && e.key === "Enter") || (e.target.value && action)){
      var { form_values } = this.state;
      if(!form_values.check_item){
        form_values.check_item = [];
      }
      form_values.check_item.push({
        title  : e.target.value,
        status : false,
      });
      this.setState({ form_values : form_values });
      e.target.value = "";
    }    
  }
  edit_todo = (row) => {
    let instance = M.Modal.getInstance(this.Modal);
    this.setState({
      form_values : JSON.parse(JSON.stringify(row)),
      edit        : row.id
    });
    instance.open();
  }
  reset_form = () => {
    var form_values = {
      id          : "",
      title       : "",
      note        : "",
      check_item  : [],
      completed   : false,
    };
    this.setState({
      form_values : form_values,
      edit        : ""
    });
  }
  remove_check_list = (id ) => {
    var { form_values }    = this.state;
    form_values.check_item.splice(id, 1);
    this.setState({ form_values : form_values });
  }

  render() {
    const { todo, form_values, page_access } = this.state;

    if(!page_access){
      return <Redirect to="/login" />;
    }
    return (
      <div className="list-wrapper flex-row">
        <div id="editpopup" ref={Modal => {this.Modal = Modal;}} className="modal">
          <div className="modal-content">
            <form action="" id="edit_form" onSubmit={e => {this.submit_form(e); e.preventDefault();}} method="post" >
              <div className="input-field">
                <input id="title" type="text" 
                value={form_values.title}
                onChange={e => this.field_change('title', e.target.value) }
                className="validate" required />
                <label htmlFor="title" className={form_values.title?"active":""}>Title</label>
                <span className="helper-text" data-error="Required field."> </span>
              </div>
              <div className="input-field">
                <textarea id="note"
                value={form_values.note} 
                onChange={e => this.field_change('note', e.target.value) }
                className="materialize-textarea"></textarea>
                <label htmlFor="note" className={form_values.note?"active":""}>Notes</label>
              </div>
            </form>
            <div className="check-list">
              <h5>Checklist</h5>
              <div className="add-new row">
                <div className="input-field col s10">
                  <input id="add_new"
                  ref={input => this.check_list = input}
                  onKeyUp={e => this.check_list_change(e) }
                  onClick={e => this.check_list_change(e, true) }
                  autoComplete="off" type="text" />
                  <label htmlFor="add_new">Add new</label>
                </div>
                <div className="col s2 add-wrapper">
                  <a onClick={ e => this.check_list.click() } className="add btn-floating btn-small waves-effect waves-light blue"><i className="material-icons">add</i></a>
                </div>
              </div>
              <div className="item-list">
                {form_values.check_item && form_values.check_item.map((row, key) => 
                  <p className="item" key={key}>
                    <label>
                      <input type="checkbox"
                      onClick={e => this.state.form_values.check_item[key]['status'] = !row.status}
                      defaultChecked={row.status} />
                      <span>{row.title}</span>
                    </label>
                    <span className="close" onClick={e => this.remove_check_list(key)}>
                      <i className="material-icons">close</i>
                    </span>
                  </p>
                )}                
              </div>
            </div>
          </div>
          <div className="modal-footer">
            <button type="submit" form="edit_form" className="btn waves-effect green">Save</button>
            <a className="modal-close waves-effect waves-green btn red ">close</a>
          </div>
        </div>
        {todo.map((row, key) => 
          <div key={key} className={row.completed?"card prj-item completed":"card prj-item"}>
            <span onClick={e => this.edit_todo(row) } className="edit"><i className="material-icons">edit</i></span>
            <span onClick={e => this.remove_todo(row) } className="remove"><i className="material-icons">close</i></span>
            <h3 className="title">{row.title}</h3>
            <div className="note">{row.note}</div>
            <div className="check-items">
              {row.check_item && row.check_item.map((sub_row, sub_key) =>
                <p key={sub_key}>
                  <label >
                    <input type="checkbox" checked={sub_row.status} onChange={(event) => this.check_list_click(row.id, sub_key) } />
                    <span>{sub_row.title}</span>
                  </label>                
                </p>
              )}
            </div>
          </div>
        )}
        <div className="fixed-action-btn">
          <a className="btn-floating btn-large blue darken-1">
            <i className="large material-icons">mode_edit</i>
          </a>
          <ul>
            <li><a onClick={e => this.remove_todo(todo, true) } className="btn-floating red darken-1 clear-all hide" title="Clear All"><i className="material-icons">clear_all</i></a></li>
            <li><a onClick={e => this.reset_form() } href="#editpopup" title="Add New" className="add-new modal-trigger btn-floating green"><i className="material-icons">add</i></a></li>
          </ul>
        </div>
      </div>
    )
  }
}
export default ToDo


